.loginForm {
    text-align: center;
    /* border: 1px solid red; */
}

.loginHdng {
    font-weight: 700;
    margin-bottom: 30px;
    color: #274e13;
    text-align: center;
}

.loginLabel {
    color: #cc0000;
    font-family: monospace;
    font-size: 16px;
    font-weight: 500;
}

.loginBtn {
    margin-top: 20px;
    height: 45px;
    color: white;
    background-color: #274e13;
    font-weight: 700;
    font-size: 21px;
    border-radius: 8px;
    border: 1px solid #274e13;
}

.loginBtn:hover {
    color: #274e13;
    background-color: white;
    border: 1px solid #274e13;
}
