    margin-right: 4px;
    /* margin:0px; */
    width: 190px;
    color: #783f04;
    text-align: center;
    font-size: 17px;
    font-weight: 700;
    background: #ddd;
    border: 2px solid #783f04;
    border-radius: 5px;
}

.Ubutton-contaner button:hover {
    background-color: #f2f2f2;
    color: #274e13;
    border: 2px solid #274e13;
}

.Uselect {
    margin-right: 1px;
    margin-left: 2px;
    box-sizing: border-box;
    width: 232px;
    height: 42px;
    padding: 5px;
    color: #783f04;
    text-align: center;
    font-size: 17px;
    font-weight: 600;
    background: #ddd;
    border: 2px solid #783f04;
    border-radius: 5px;
}

.Uselect:hover {
    background-color: #f2f2f2;
    color: #274e13;
    border: 2px solid #274e13;
}

.Usearch-input {
    margin-right: 2px;
    margin-left: 2px;
    background: #ddd;
    width: 190px;
    color: #274e13;
    padding: 7px;
    border: 2px solid #783f04;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    border-right: none;
}

.Usearch-input:hover {
    background-color: #f2f2f2;
    border: 2px solid #274e13;
    border-right: none;
}

.Usearch-button {
    color: #783f04;
}

.Utbl {
    height: 393px;
}

.newUHdng {
    font-weight: 700;
    margin-bottom: 20px;
    color: #274e13;
    text-align: center;
}

.URegisterBtn {
    padding: 3px 120px 3px 120px;
    margin-top: 5px;
    height: 45px;
    color: white;
    background-color: #274e13;
    font-weight: 500;
    font-size: 21px;
    border-radius: 8px;
    border: 1px solid #274e13;
}

.URegisterBtn:hover {
    color: #274e13;
    background-color: white;
    border: 1px solid #274e13;
}

.label-U {
    font-weight: bold;
    color: #783f04;
}

